@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../fonts/.woff2") format("woff2"), url("../fonts/.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-align: center;
  border-radius: 3px;
  color: #F2F5F6;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  position: fixed;
  padding: 20px 0 25px;
  display: flex;
  min-height: 120px;
  width: 100%;
  align-items: center;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  color: #F2F5F6;
  z-index: 2;
}
@media (max-width: 1023px) {
  .header {
    padding: 10px 0 15px;
    min-height: 100px;
  }
}

.header__container {
  display: flex;
  align-items: center;
}

.header__contacts {
  margin: 0 auto;
  padding: 0 10px;
  display: flex;
  flex-wrap: wrap;
  column-gap: 50px;
  max-width: 100%;
}
@media (max-width: 1023px) {
  .header__contacts {
    margin-right: 20px;
    column-gap: 40px;
  }
}

.header__link {
  display: flex;
  align-items: center;
  color: inherit;
  column-gap: 15px;
  transition: 0.3s;
}
.header__link:hover {
  color: #00B2FF;
}
.header__link:hover svg {
  fill: #F2F5F6;
}
@media (max-width: 1023px) {
  .header__link {
    display: inline-block;
  }
  .header__link span {
    display: none;
  }
}

.header__link svg {
  width: 20px;
  height: 20px;
  fill: #00B2FF;
  transition: 0.3s;
}

.header__btn {
  margin-right: 50px;
  padding: 11px 15px;
  min-height: 40px;
  background-color: transparent;
  border: 1px solid #F2F5F6;
  text-transform: uppercase;
  cursor: pointer;
  transition: 0.3s;
}
.header__btn:hover {
  color: #00B2FF;
  border-color: #00B2FF;
}
@media (max-width: 1023px) {
  .header__btn {
    display: none;
  }
}

.hero {
  padding-top: 305px;
  padding-bottom: 280px;
  background-color: #041827;
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url(../../img/background-desktop.jpg) center/cover no-repeat;
  color: #F2F5F6;
}
@media (max-width: 1023px) {
  .hero {
    padding-top: 180px;
    padding-bottom: 205px;
  }
}

.hero__container {
  display: flex;
  flex-direction: column;
}

.hero__content {
  padding-left: 130px;
}
@media (max-width: 1023px) {
  .hero__content {
    padding-left: 0;
  }
}
.hero__content h2 {
  margin: 0 0 30px 0;
  font-family: "Roboto Condensed";
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;
  letter-spacing: 0.07em;
}
@media (max-width: 1023px) {
  .hero__content h2 {
    margin-bottom: 0;
    font-size: 64px;
    line-height: 75px;
  }
}
.hero__content p {
  margin-bottom: 50px;
  max-width: 700px;
  font-size: 22px;
  line-height: 34px;
}
@media (max-width: 1023px) {
  .hero__content p {
    margin-top: 0;
    margin-bottom: 55px;
  }
}

.hero__btn {
  padding: 17px;
  min-height: 55px;
  font-size: 18px;
  line-height: 21px;
  background-color: #00B2FF;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .hero__btn {
    margin-left: 50px;
  }
}

.advantages {
  color: #F2F5F6;
  padding: 120px 0;
  background-color: #f8fdff;
}
@media (max-width: 1023px) {
  .advantages {
    padding: 80px 0;
  }
}

.advantages__content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}

.item-advantage {
  padding: 40px 45px 35px;
  max-width: 595px;
  min-height: 300px;
  border: 1px solid #EBEBEB;
  border-left: 5px solid #00B2FF;
  transition: 0.3s;
}
.item-advantage:hover {
  background-color: #00B2FF;
}
.item-advantage:hover svg {
  fill: #f8fdff;
}
.item-advantage:hover h4 {
  color: #F2F5F6;
}
.item-advantage:hover p {
  color: #f8fdff;
}
@media (max-width: 1023px) {
  .item-advantage {
    min-height: 360px;
  }
}
.item-advantage svg {
  margin-bottom: 10px;
  width: 35px;
  height: 35px;
  fill: #00B2FF;
}
.item-advantage h4 {
  margin-top: 0;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00B2FF;
}
@media (max-width: 1023px) {
  .item-advantage h4 {
    margin-bottom: 10px;
  }
}
.item-advantage p {
  margin-top: 0;
  line-height: 22px;
  color: #041827;
}

.about {
  padding-bottom: 100px;
}

.about__container {
  position: relative;
  display: flex;
}
.about__container::before {
  position: absolute;
  top: 50px;
  left: -40px;
  content: "2023";
  font-family: "Roboto Condensed", sans-serif;
  font-size: 155px;
  font-weight: 700;
  line-height: 221px;
  transform: rotate(-90deg);
  background: #041827 url(../../img/about-bg.png) no-repeat;
  background-clip: text;
  color: transparent;
}

.about__text {
  padding-left: 255px;
  padding-bottom: 20px;
  color: #041827;
}
.about__text h3 {
  margin-top: 0;
  margin-bottom: 10px;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 51px;
}
.about__text p {
  margin-top: 0;
}

.about__btn {
  margin-top: 15px;
  padding: 13px 30px;
  min-height: 45px;
  background-color: transparent;
  color: #00B2FF;
  border: 1px solid #00B2FF;
  text-transform: uppercase;
  transition: 0.3s;
}
.about__btn:hover {
  color: #F2F5F6;
  background-color: #041827;
}

.services {
  padding: 65px 0 140px;
  background-color: #041827;
  color: #F2F5F6;
}

.services__container h3 {
  margin-top: 0;
  margin-bottom: 55px;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 36px;
  line-height: 51px;
}

.services__content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 85px;
}
.services__content h5 {
  margin-top: 20px;
  padding-right: 30px;
  max-width: 230px;
  font-size: 20px;
  line-height: 1.5;
  font-weight: 400;
}

.services__item-link {
  position: relative;
  padding: 30px 40px 60px;
  display: inline-block;
  max-width: 350px;
  width: 100%;
  min-height: 650px;
  color: #F2F5F6;
  background-color: #041827;
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url(../../img/background-desktop.jpg) no-repeat;
  background-position: 0 0;
  background-size: cover;
}
.services__item-link:hover {
  background: linear-gradient(2deg, rgba(25, 255, 2, 0) 71.72%, rgba(125, 22, 36, 0) 100%), url(../../img/background-desktop.jpg);
  background-size: cover;
}
.services__item-link::before {
  position: absolute;
  content: ">";
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background-color: #00B2FF;
  border-radius: 3px;
}
.services__item-link:first-child {
  background-position: 15%;
}
.services__item-link:nth-child(2) {
  background-position: 45%;
}
.services__item-link:nth-child(3) {
  background-position: 80%;
}

